.board .columns {
  display: flex;
  justify-content: center;
  height: 100%;
  min-width: 90%;
  overflow-x: scroll;
  overflow-y: visible;
}

.board {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #e8e5da;
  min-width: fit-content;
  width: 100%;
  min-height: 800px;
}

.board > h1 {
  box-shadow: black;
  padding: 8px;
  font-size: 36px;
}
